.badge {
  line-height: unset;
}

.shadow {
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.25) !important;
}

.badge-primary {
  color: white;
  background-color: #7c56fb;
}

.badge-secondary {
  color: white;
  background-color: #459488;
}

.badge-success {
  color: white;
  background-color: #4a98e9;
}

.badge-danger {
  color: black;
  background-color: #ceda49;
}

.badge-warning {
  color: black;
  background-color: #f5c150;
}

.badge-info {
  color: white;
  background-color: #6e757c;
}

.badge-light {
  color: white;
  background-color: #152949;
}

.badge-dark {
  color: black;
  background-color: #ddd;
}

.navbar {
  background: linear-gradient(to right, #152443, #3c62b4) !important;
}

.navbar-dark .navbar-nav .nav-link.disabled {
  color: #fff;
}

.Toastify__toast--default {
  background: #fff;
  color: #000;
}

.small-article-card-title-truncate {
  display: inline;
}

.small-article-card-title-truncate div {
  display: inline;
}

@keyframes zoomIn {
  from {
    opacity: 0;
    transform: scale3d(0.3, 0.3, 0.3);
  }

  50% {
    opacity: 1;
  }
}

.zoomIn {
  animation-name: zoomIn;
}

@keyframes zoomOut {
  from {
    opacity: 1;
  }

  50% {
    opacity: 0;
    transform: scale3d(0.3, 0.3, 0.3);
  }

  to {
    opacity: 0;
  }
}

.zoomOut {
  animation-name: zoomOut;
}

.animate {
  animation-duration: 700ms;
}

@media (min-width: 1200px) and (max-width: 1900px) {
  .badge-time-row {
    font-size: 0.75rem;
  }
}
